.items {
	padding: 57px 0 25px;
	@include sm {
		padding: 23px 0 13px; }
	.pagination {
		margin-bottom: 23px; }
	&_gray {
		background-color: #F2F2F2; }
	&__col {
		margin-bottom: 45px;
		@include sm {
			margin-bottom: 22px; } }
	&__filter {
		margin-bottom: 70px;
		@include clr;
		@include md {
			margin-bottom: 43px; } }
	&__title {
		margin-top: 20px;
		@include f(30px,300,1.2);
		float: left;
		@include md {
			font-size: 20px; }
		@include xs {
			margin: 0 0 15px;
			float: none; } }
	&__right {
		float: right;
		@include xs {
			float: none; } }
	&__select {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 28px;
			@include xs {
				margin: 15px 0 0; } }
		@include xs {
			display: block; } } }
.item {
	color: $black;
	height: 100%;
	transition: color .3s, background-color .3s;
	display: block;
	&:hover {
		color: $blue;
		background-color: $white; }
	&__img {
		width: 100%;
		padding-bottom: 100%;
		background-position: center;
		background-size: cover;
		display: block; }
	&__wrap {
		padding-top: 13px;
		display: block;
		@include sm {
			padding-top: 10px; }
		.item_simple & {
			padding: 13px 19px 18px; } }
	&__title {
		@include f(20px,300,1.2);
		letter-spacing: 0.03em;
		display: block;
		@include sm {
			font-size: 14px; } }
	&__type {
		@include f(17px,200,1.15);
		display: block; } }
