.topic {
	padding: 56px 0 65px;
	color: rgba($black, .6);
	background-color: #F2F2F2;
	@include sm {
		padding: 28px 0 32px; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__inner {
		&_horizontal {
			display: flex;
			justify-content: space-between;
			align-items: center;
			@include md {
				flex-direction: column; }
			&:after {
				display: none; }
			p {
				max-width: 631px;
				margin-left: 10px; } } }
	&__title {
		margin-bottom: 30px;
		@include sm {
			margin-bottom: 15px; }
		&_wm {
			margin-bottom: 0;
			@include sm {
				margin-bottom: 0; } } }
	&__number {
		font-family: $circe;
		font-weight: 400;
		color: $black; }
	&__date {
		font-size: 25px;
		font-weight: 300;
		font-family: $circe;
		@include sm {
			font-size: 18px; } }
	&__right {
		.up-text {
			margin-bottom: 14px; } } }

