.inner {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1140px;
	@include clr;
	@include sm {
		padding: 0 30px; }
	@include xs {
		padding: 0 15px; } }

.row {
	margin: 0 -15px;
	display: flex;
	flex-wrap: wrap; }

.col {
	padding: 0 15px;
	&-16 {
		flex: 0 0 16.6%;
		max-width: 16.6%; }
	&-20 {
		flex: 0 0 20%;
		max-width: 20%; }
	&-25 {
		flex: 0 0 25%;
		max-width: 25%; }
	&-33 {
		flex: 0 0 33.3%;
		max-width: 33.3%; }
	&-50 {
		flex: 0 0 50%;
		max-width: 50%; }
	&-66 {
		flex: 0 0 66.7%;
		max-width: 66.7%; }
	&-100 {
		flex: 0 0 100%;
		max-width: 100%; }

	&-md-16 {
		@include md {
			flex: 0 0 16.6%;
			max-width: 16.6%; } }
	&-md-25 {
		@include md {
			flex: 0 0 25%;
			max-width: 25%; } }
	&-md-33 {
		@include md {
			flex: 0 0 33.3%;
			max-width: 33.3%; } }
	&-md-50 {
		@include md {
			flex: 0 0 50%;
			max-width: 50%; } }
	&-md-100 {
		@include md {
			flex: 0 0 100%;
			max-width: 100%; } }

	&-sm-33 {
		@include sm {
			flex: 0 0 33.3%;
			max-width: 33.3%; } }
	&-sm-50 {
		@include sm {
			flex: 0 0 50%;
			max-width: 50%; } }
	&-sm-100 {
		@include sm {
			flex: 0 0 100%;
			max-width: 100%; } }

	&-xs-50 {
		@include xs {
			flex: 0 0 50%;
			max-width: 50%; } }
	&-xs-100 {
		@include xs {
			flex: 0 0 100%;
			max-width: 100%; } }
	&-right {
		margin-left: auto; } }
