.slider-wrap {
	position: relative;
	height: 679px;
	overflow: hidden;
	@include xs {
		height: auto; }
	&__out {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 2;
		padding-top: 110px;
		pointer-events: none;
		@include xs {
			position: relative;
			padding: 0;
			margin: 0 -15px; } }
	&__wind {
		float: right; } }
.wind {
	width: 255px;
	color: $white;
	pointer-events: initial;
	background-color: #3E3F43;
	@include md {
		margin-right: 45px; }
	@include r(700) {
		margin-right: 0; }
	@include xs {
		width: 100%; }
	&__head {
		padding: 25px 33px; }
	&__title {
		margin-bottom: 15px;
		text-align: center;
		@include f(17px,700,1.2); }
	&__item {
		position: relative;
		padding: 7px 25px 10px 8px;
		position: relative;
		@include f(15px,200,1);
		min-height: 34px;
		user-select: none;
		cursor: pointer;
		display: block;
		&:not(:last-child) {
			border-bottom: 1px solid rgba($white, .05); }
		input {
			position: absolute;
			opacity: 0;
			&:checked {
				& ~ .wind__check {
					opacity: 1; }
				& ~ .wind__text {
					font-weight: 300; } } } }
	&__check {
		position: absolute;
		top: 50%;
		right: 10px;
		margin-top: -5px;
		font-size: 0;
		opacity: 0;
		transition: opacity .3s;
		.icon {
			font-size: 9px; } }
	&__text {
		transition: font-weight .1s; }
	&__body {
		padding: 38px 33px 32px;
		background-color: #4A4B50; }
	&__name {
		padding: 8px 12px 12px;
		text-align: center;
		@include f(17px,700,1.2);
		border: 1px solid rgba($white, .1); }
	&__trigger {
		margin-top: 20px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: #4A4A4A; }
	&__btn {
		position: relative;
		width: 62px;
		height: 62px;
		color: $white;
		transition: background-color .3s;
		background-color: $blue;
		@include hover {
			background-color: #C8C8C8; }
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			font-size: 10px; } }
	&__numb {
		@include f(26px,700,1.2); } }
.slider {
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	&.slick-initialized {
		opacity: 1;
		visibility: visible; }
	&__slide {
		position: relative;
		padding: 89px 0;
		height: 679px;
		@include vertical;
		@include xs {
			padding: 15px 0;
			height: 520px; } }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-size: cover;
		background-position: center; }
	&__inner {
		width: 100%;
		position: relative;
		vertical-align: bottom;
		text-align: left;
		display: inline-block; }
	&__content {
		padding: 28px 39px 36px;
		font-size: 17px;
		line-height: 1.4;
		max-width: 421px;
		background-color: #F9F9F9;
		@include md {
			max-width: 320px; }
		@include xs {
			max-width: 100%; } }
	&__title {
		margin-bottom: 10px;
		@include f(20px,300,1.2); }
	&__footer {
		margin-top: 30px; } }
